"use client";

import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { useAuthStore } from "../store/authStore";
import { useProfileStore } from "../store/profileStore";
import { profileApi, problemsApi, insightsApi, graphApi } from "../lib/api";
import { Profile, Problem, Recommendation } from "../lib/types";
import {
    BarChart2, Brain, GitBranch, BookOpen, ArrowRight,
    TrendingUp, TrendingDown, Zap, Star, AlertTriangle, CheckCircle
} from "lucide-react";

const DIFF_LABELS = ["", "Easy", "Easy-Med", "Medium", "Hard", "Expert"];
const DIFF_COLORS = ["", "text-green-500", "text-lime-500", "text-yellow-500", "text-red-500", "text-purple-500"];
const DIFF_BG = ["", "bg-green-50", "bg-lime-50", "bg-yellow-50", "bg-red-50", "bg-purple-50"];

const EXP_COLORS: Record<string, string> = {
    beginner: "bg-green-100 text-green-700",
    intermediate: "bg-yellow-100 text-yellow-700",
    advanced: "bg-purple-100 text-purple-700",
};

function SkillBar({ name, score }: { name: string; score: number }) {
    return (
        <div>
            <div className="flex justify-between text-xs mb-1">
                <span className="text-[#374151] font-medium">{name}</span>
                <span className="text-[#9ca3af]">{Math.round(score * 100)}%</span>
            </div>
            <div className="h-1.5 bg-[#f3f4f6] rounded-full overflow-hidden">
                <motion.div
                    className={`h-full rounded-full ${score >= 0.7 ? "bg-[#22c55e]" : score >= 0.4 ? "bg-[#f97316]" : "bg-[#ef4444]"}`}
                    initial={{ width: 0 }}
                    animate={{ width: `${score * 100}%` }}
                    transition={{ duration: 0.8, delay: 0.2 }}
                />
            </div>
        </div>
    );
}

export default function DashboardPage() {
    const router = useRouter();
    const { isAuthenticated, user } = useAuthStore();
    const { profile, setProfile } = useProfileStore();
    const [problems, setProblems] = useState<Problem[]>([]);
    const [recommendations, setRecommendations] = useState<Recommendation[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if (!isAuthenticated) { router.push("/login"); return; }
        (async () => {
            try {
                const [profileRes, problemsRes, recsRes] = await Promise.all([
                    profileApi.get(),
                    problemsApi.list(),
                    graphApi.recommendations().catch(() => ({ data: [] })),
                ]);
                setProfile(profileRes.data);
                setProblems(problemsRes.data?.slice(0, 6) || []);
                setRecommendations(recsRes.data?.slice(0, 3) || []);
            } catch {
                // silently handle
            } finally {
                setLoading(false);
            }
        })();
    }, [isAuthenticated, router, setProfile]);

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-[#faf9f6] pt-16">
                <div className="text-center">
                    <div className="w-10 h-10 border-2 border-[#f97316] border-t-transparent rounded-full animate-spin mx-auto mb-4" />
                    <p className="text-[#6b7280] text-sm">Loading your profileâ€¦</p>
                </div>
            </div>
        );
    }

    const topSkills = Object.entries(profile?.skills || {})
        .sort(([, a], [, b]) => b - a)
        .slice(0, 8);

    const hour = new Date().getHours();
    const greeting = hour < 12 ? "Good morning" : hour < 17 ? "Good afternoon" : "Good evening";

    return (
        <div className="min-h-screen bg-[#faf9f6] pt-20">
            <div className="max-w-7xl mx-auto px-6 py-10">

                {/* Header */}
                <motion.div
                    className="mb-10"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5 }}
                >
                    <p className="text-[#9ca3af] text-sm mb-1">{greeting},</p>
                    <h1 className="font-serif font-black text-4xl md:text-5xl text-[#0d0d0d]">
                        {user?.name?.split(" ")[0]}
                        <span className="text-[#f97316]">.</span>
                    </h1>
                    <div className="flex items-center gap-3 mt-3">
                        {profile?.experience_level && (
                            <span className={`text-xs font-semibold px-2.5 py-1 rounded-full ${EXP_COLORS[profile.experience_level]}`}>
                                {profile.experience_level}
                            </span>
                        )}
                        {profile?.preferred_style && (
                            <span className="text-xs text-[#9ca3af]">
                                {profile.preferred_style} learner
                            </span>
                        )}
                    </div>
                </motion.div>

                {/* Stats strip */}
                <motion.div
                    className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5, delay: 0.1 }}
                >
                    {[
                        { icon: BookOpen, val: profile?.submissions_count || 0, label: "Submissions", color: "text-[#f97316]" },
                        { icon: Star, val: profile?.strengths?.length || 0, label: "Strengths", color: "text-yellow-500" },
                        { icon: AlertTriangle, val: profile?.gaps?.length || 0, label: "Gaps", color: "text-red-500" },
                        { icon: CheckCircle, val: Object.keys(profile?.skills || {}).length, label: "Concepts Tracked", color: "text-green-500" },
                    ].map((stat, i) => (
                        <div key={i} className="bg-white border border-[#e5e7eb] rounded-xl p-5 card-hover">
                            <stat.icon className={`w-5 h-5 ${stat.color} mb-3`} />
                            <div className="font-serif font-bold text-3xl text-[#0d0d0d]">{stat.val}</div>
                            <div className="text-xs text-[#9ca3af] mt-0.5">{stat.label}</div>
                        </div>
                    ))}
                </motion.div>

                <div className="grid lg:grid-cols-3 gap-6">
                    {/* Skill bars */}
                    <motion.div
                        className="lg:col-span-1 bg-white border border-[#e5e7eb] rounded-2xl p-6"
                        initial={{ opacity: 0, x: -20 }}
                        animate={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.5, delay: 0.2 }}
                    >
                        <div className="flex items-center gap-2 mb-5">
                            <BarChart2 className="w-4 h-4 text-[#f97316]" />
                            <h2 className="font-semibold text-sm text-[#374151] uppercase tracking-wider">Skill Map</h2>
                        </div>
                        {topSkills.length > 0 ? (
                            <div className="flex flex-col gap-3">
                                {topSkills.map(([name, score]) => (
                                    <SkillBar key={name} name={name} score={score} />
                                ))}
                            </div>
                        ) : (
                            <p className="text-sm text-[#9ca3af]">
                                Complete a few problems to see your skill map.
                            </p>
                        )}

                        <div className="mt-6 pt-4 border-t border-[#f3f4f6]">
                            <p className="text-xs font-semibold text-[#374151] mb-2">Known Gaps</p>
                            <div className="flex flex-wrap gap-1.5">
                                {(profile?.gaps || []).slice(0, 5).map((g) => (
                                    <span key={g} className="text-xs bg-red-50 text-red-600 border border-red-100 px-2 py-0.5 rounded-full">
                                        {g}
                                    </span>
                                ))}
                                {!profile?.gaps?.length && <span className="text-xs text-[#9ca3af]">None yet</span>}
                            </div>
                        </div>
                    </motion.div>

                    {/* Right panel */}
                    <div className="lg:col-span-2 flex flex-col gap-6">

                        {/* Insights card */}
                        {profile?.insights && (
                            <motion.div
                                className="bg-[#0d0d0d] rounded-2xl p-6 text-white"
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ duration: 0.5, delay: 0.25 }}
                            >
                                <div className="flex items-center gap-2 mb-4">
                                    <Brain className="w-4 h-4 text-[#f97316]" />
                                    <span className="text-xs font-bold uppercase tracking-wider text-[#f97316]">
                                        AI Insights
                                    </span>
                                </div>
                                <p className="text-white/80 text-sm leading-relaxed mb-4">
                                    {profile.insights.motivational_note}
                                </p>
                                <div className="grid grid-cols-2 gap-3">
                                    {profile.insights.improving_concepts?.slice(0, 2).map((c) => (
                                        <div key={c} className="flex items-center gap-2 text-xs text-white/60">
                                            <TrendingUp className="w-3 h-3 text-green-400" /> {c}
                                        </div>
                                    ))}
                                    {profile.insights.declining_concepts?.slice(0, 2).map((c) => (
                                        <div key={c} className="flex items-center gap-2 text-xs text-white/60">
                                            <TrendingDown className="w-3 h-3 text-red-400" /> {c}
                                        </div>
                                    ))}
                                </div>
                                <div className="mt-4 pt-3 border-t border-white/10">
                                    <p className="text-xs text-white/40">Focus areas:</p>
                                    <div className="flex flex-wrap gap-1.5 mt-1.5">
                                        {(profile.insights.recommended_focus || []).map((f) => (
                                            <span key={f} className="text-xs bg-[#f97316]/20 text-[#f97316] border border-[#f97316]/30 px-2 py-0.5 rounded-full">
                                                {f}
                                            </span>
                                        ))}
                                    </div>
                                </div>
                            </motion.div>
                        )}

                        {/* Recommended problems */}
                        <motion.div
                            className="bg-white border border-[#e5e7eb] rounded-2xl p-6"
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ duration: 0.5, delay: 0.3 }}
                        >
                            <div className="flex items-center justify-between mb-5">
                                <div className="flex items-center gap-2">
                                    <Zap className="w-4 h-4 text-[#f97316]" />
                                    <h2 className="font-semibold text-sm text-[#374151] uppercase tracking-wider">
                                        Suggested Problems
                                    </h2>
                                </div>
                                <Link href="/problems" className="text-xs text-[#f97316] font-medium no-underline hover:underline flex items-center gap-1">
                                    See all <ArrowRight className="w-3 h-3" />
                                </Link>
                            </div>
                            <div className="flex flex-col gap-2">
                                {problems.slice(0, 5).map((p) => (
                                    <Link
                                        key={p.id}
                                        href={`/problems/${p.id}`}
                                        className="flex items-center justify-between px-4 py-3 rounded-xl hover:bg-[#f9fafb] transition-colors no-underline group"
                                    >
                                        <span className="text-sm text-[#374151] font-medium group-hover:text-[#0d0d0d] transition-colors">
                                            {p.title}
                                        </span>
                                        <div className="flex items-center gap-2">
                                            <span className={`text-xs font-semibold px-2 py-0.5 rounded-full ${DIFF_BG[p.difficulty]} ${DIFF_COLORS[p.difficulty]}`}>
                                                {DIFF_LABELS[p.difficulty]}
                                            </span>
                                            <ArrowRight className="w-3.5 h-3.5 text-[#d1d5db] group-hover:text-[#f97316] transition-colors" />
                                        </div>
                                    </Link>
                                ))}
                            </div>
                        </motion.div>
                    </div>
                </div>

                {/* Quick links */}
                <motion.div
                    className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-6"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5, delay: 0.4 }}
                >
                    {[
                        { href: "/problems", icon: BookOpen, label: "Browse Problems", sub: "50+ DSA problems" },
                        { href: "/graph", icon: GitBranch, label: "Knowledge Graph", sub: "30 concepts mapped" },
                        { href: "/profile", icon: BarChart2, label: "My Profile", sub: "Skills & history" },
                        { href: "#", icon: Brain, label: "Ask AI Tutor", sub: "Socratic engine" },
                    ].map((item, i) => (
                        <Link
                            key={i}
                            href={item.href}
                            className="bg-white border border-[#e5e7eb] rounded-xl p-5 card-hover no-underline group"
                        >
                            <item.icon className="w-6 h-6 text-[#f97316] mb-3 group-hover:scale-110 transition-transform" />
                            <p className="font-semibold text-sm text-[#0d0d0d]">{item.label}</p>
                            <p className="text-xs text-[#9ca3af] mt-0.5">{item.sub}</p>
                        </Link>
                    ))}
                </motion.div>
            </div>
        </div>
    );
}
